<template>
  <v-row align="center" justify="center">
    <v-col cols="8">
      <v-alert type="info"> Router value:{{ $route.params }} </v-alert>
      <v-sheet height="500" class="mr-4">
        <v-btn class="ma-2" @click="$refs.calendar.prev()">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <v-btn class="ma-2" @click="$refs.calendar.next()">
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
        <v-calendar
          ref="calendar"
          :events="events"
          color="primary"
          type="month"
          v-model="clickDate"
        ></v-calendar>
      </v-sheet>
    </v-col>
  </v-row>
</template>
<script>
export default {
  name: 'Calendar',
  components: {
  },
  data () {
    return {
      clickDate: '',
      events: [
        {
          name: '',
          start: '2020-06-19'
        },
        {
          name: '',
          start: '2020-06-20'
        },
        {
          name: '',
          start: '2020-06-21'
        },
        {
          name: '',
          start: '2020-06-22'
        },
        {
          name: '',
          start: '2020-06-23'
        },
        {
          name: '',
          start: '2020-06-24'
        },
        {
          name: '',
          start: '2020-06-25'
        }
      ]
    }
  },
  watch: {
    clickDate: function (date) {
      // ajax NOT here => date.vue mounted function
      if (this.events.filter((x) => { return x.start === date }).length === 1) {
        this.$emit('step-change', 3)
        this.$router.push({ path: `/${this.$route.params.sport}/${date}` })
      }
    }
  }
}
</script>
